import React from 'react'
import { getSession } from '../../sessions.server';

// Loader - redirect if already logged in
export async function loader({ request }) {
  const session = await getSession(request);
  const user = session.get("user");
  if (user) return redirect("/dashboard");
  return null;
}

// Action - handle login form submission
export async function action({ request }) {
    const formData = await request.formData();
    const username = formData.get("username");
    const password = formData.get("password");

    try {
        const user = await fetchUserData(username);

        // Compare passwords (plain for now)
        if (user.password !== password) {
            return { error: "Invalid username or password" };
        }

        // Create session
        const session = await getSession(request);
        session.set("user", { id: user.id, name: user.name, username });

        return redirect("/dashboard", {
            headers: {
                "Set-Cookie": await commitSessio(session),
            },
        });
    } catch (err) {
        return { error: err.message || "Something went wrong" };
    }
}

function Login() {

    return (
        <div className='flex h-full items-center justify-center max-w-[600px] mx-auto p-6'>
            <div className='flex flex-col items-center gap-6 w-full'>
                <img src='/images/sous-chef.svg' alt='sous-logo' width="100px" />
                <div className="flex justify-between items-center gap-x-4 w-full">
                    Login Account
                    <img src="/images/iss_logo.webp" alt="iss-logo" width="60px" />
                </div>
            </div>
        </div>
    )
}

export default Login
